<template>
    <b-card bg-variant="light">
        <div class="web-camera-container">
        <b-row class="no-gutters">
            <b-col class="col-XS-12">
                <div v-show="isCameraOpen && isLoading" class="camera-loading">
                    <ul class="loader-circle">
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </div>
                <!-- <div v-if="isCameraOpen" v-show="!isLoading" class="camera-box" :class="{ 'flash' : isShotPhoto }">
                    <div class="camera-shutter" :class="{'flash' : isShotPhoto}"></div> -->
                <div class="camera-box" :class="{ 'flash' : isShotPhoto }">
                    <div class="camera-shutter" :class="{'flash' : isShotPhoto}"></div>
                    <!-- <video id="video" v-show="!isPhotoTaken" ref="camera" :width="450" :height="337.5" autoplay></video>
                    <canvas v-show="isPhotoTaken" id="photoTaken" ref="canvas" :width="450" :height="337.5"></canvas> -->
                    <!-- <video id="video" v-show="!isPhotoTaken" ref="video" :width=width :height=height autoplay></video>
                    <canvas v-show="isPhotoTaken" id="photoTaken" ref="canvas" :width=width :height=height></canvas> -->
                    <video id="video" v-show="!isPhotoTaken" ref="video" autoplay muted playsinline></video>
                    <canvas v-show="isPhotoTaken" id="photoTaken" ref="canvas" :width=width :height=height ></canvas>
                </div>
            </b-col>
        </b-row>
        <b-row class="no-gutters">
            <b-col class="col-XS-8 text-center">
                <div v-if="isCameraOpen && !isLoading">
                    <b-button variant="info"><b-icon icon="camera" font-scale="2" @click="takePhoto"></b-icon></b-button>
                </div>
            </b-col>
            <b-col class="col-XS-3 text-center" style="width:90px;">
                <div v-if="isPhotoTaken && isCameraOpen">
                    <b-button><b-icon icon="cloud-upload" font-scale="2" @click="uploadImage()" download="my-photo.jpg"></b-icon></b-button>
                </div>
            </b-col>
            <b-col class="col-XS-1 text-center">
                <div v-if="isCameraOpen && !isLoading">
                <b-button variant="danger"><b-icon icon="house-door" font-scale="2" @click="clickMenuBack"></b-icon></b-button>
                </div>
            </b-col>
        </b-row>
        </div>
    </b-card>
</template>

<style scoped>
