<script>
import axios from 'axios'
import apiUrls from '../common/apiUrls'
import commonMethods from '../common/commonMethods'

export default {
    data() {
        return {
            isCameraOpen: false,
            isPhotoTaken: false,
            isShotPhoto: false,
            isLoading: false,
            link: '#',
            // width: 600,
            // height: 337.5,
            width: 800,
            height: 800,
        }
    },
    created: function() {
        // if(!this.$store.state.loginData.accessToken) {
        //     this.$router.push('/')
        // }
        // console.log("uploadPhone created: ");
        // this.isCameraOpen = true;
        // this.createCameraElement();
        // this.getCameraElementSize();
        this.toggleCamera();
    },
    beforeDestroy: function() {
        if (this.isCameraOpen) {
            this.toggleCamera();
        }
    },
    methods: {
        clickMenuBack() {
            commonMethods.clickMenuBack();
        },
        async toggleCamera() {
            if (this.isCameraOpen) {
                this.isCameraOpen = false;
                this.isPhotoTaken = false;
                this.isShotPhoto = false;
                this.stopCameraStream();
                // console.log('toggleCamera() true this.width=' + this.width + ', this.height=' + this.height);
            } else {
                this.isCameraOpen = true;
                this.createCameraElement();
                this.getCameraElementSize();
                // console.log('toggleCamera() false this.width=' + this.width + ', this.height=' + this.height);
            }
        },
    
        createCameraElement() {
            this.isLoading = true;
            // var video = document.querySelector('video');
            // // video.muted = true;
            // // video.play()
            // video.setAttribute('autoplay', '');
            // video.setAttribute('muted', '');
            // video.setAttribute('playsinline', '');
            const constraints =  {
                audio: false,
                // video: true
                video: {
                //   width: this.width, height: this.height,
                  // width: 640, height: 480,
                  // facingMode: "user",    // インカメラ
                  facingMode: "environment",    // 背面カメラ
                  // facingMode: { exact: "environment" },  // NG！
               }
            };
            // getUserMedia によるカメラ映像の取得
            navigator.mediaDevices
            .getUserMedia(constraints)
            //リアルタイムに再生（ストリーミング）させるためにビデオタグに流し込む
            .then(stream => {
                this.isLoading = false;
                this.$refs.video.srcObject = stream;
            })
            .catch(error => {
                this.isLoading = false;
                alert("May the browser didn't support or there is some errors. error=" + error);
            });
        },

        async getCameraElementSize() {
                // console.log('getCameraElementSize() 1 this.width=' + this.width + ', this.height=' + this.height);
                let element = document.getElementById("video");
                // let intervalId = setInterval( function () {
                let n = 0;
                while (n < 20) {
                    await commonMethods.wait(500);  // 500m秒待機
                    // console.log('getCameraElementSize() 2A this.width=' + this.width + ', this.height=' + this.height);
                    if (element == null) {
                        element = document.getElementById("video");
                    } else {
                        if (element.readyState != null) {
                            if ( element.readyState >= HTMLMediaElement.HAVE_METADATA ) {
                                this.width = element.videoWidth;
                                this.height = element.videoHeight;
                                // console.log('getCameraElementSize() 2B this.width=' + this.width + ', this.height=' + this.height);
                                // alert('getCameraElementSize() 2B this.width=' + this.width + ', this.height=' + this.height);
                                break;
                                // clearInterval( intervalId ) ;
                            }
                        }
                    }
                    n += 1;
                }
                // }, 500 ) ;
                // console.log('getCameraElementSize() 3 this.width=' + this.width + ', this.height=' + this.height);            
        },
    
        stopCameraStream() {
            let tracks = this.$refs.video.srcObject.getTracks();
            tracks.forEach(track => {
                track.stop();
            });
        },
    
        takePhoto() {
            if(!this.isPhotoTaken) {
                this.isShotPhoto = true;
                const FLASH_TIMEOUT = 50;
                setTimeout(() => {
                    this.isShotPhoto = false;
                }, FLASH_TIMEOUT);
            }
            this.isPhotoTaken = !this.isPhotoTaken;
            const context = this.$refs.canvas.getContext('2d');
            // context.drawImage(this.$refs.video, 0, 0, 450, 337.5);
            // context.drawImage(this.$refs.video, 0, 0, 200, 337.5, 0, 0, 200, 337.5);
            // console.log('takePhoto() this.width=' + this.width + ', this.height=' + this.height);
            context.drawImage(this.$refs.video, 0, 0, this.width, this.height);
        },

        dataUrlToBlob(dataUrl){
            let arr = dataUrl.split(",");
            let mime = arr[0].match(/:(.*?);/)[1];
            let bstr = atob(arr[1]);
            let n = bstr.length;
            let u8arr = new Uint8Array(n);
            while(n--){
                u8arr[n] = bstr.charCodeAt(n);
            }
            let imgFile = new Blob([u8arr], {type:mime});
            return imgFile;
        },

        async uploadImage(){
            // console.log('uploadImage start !');
        
            var destinationCanvas = document.getElementById("photoTaken");
            const context = destinationCanvas.getContext('2d');
            context.drawImage(destinationCanvas, this.width, this.height);
          
            var imagebase64data = destinationCanvas.toDataURL();
            let blob = this.dataUrlToBlob(imagebase64data);
          
            let formData = new FormData();
            formData.append("image", blob);
            
            let axiosConfigObject = {
                headers : {
                    Authorization:  'Bearer ' +  this.$store.state.loginData.accessToken,   
                    'Content-Type' : 'multipart/form-data'
                }
            }   
            await axios.post(apiUrls.savePhoto, formData, axiosConfigObject).then(response => {      
                alert(commonMethods.getResponseMessage(response)); 
                if (response.data.resultCode == '000'){
                    this.takePhoto();
                }    
            })
            .catch(error => {
                // console.log('error=' + error);
                alert('例外が発生しました。(' + commonMethods.getErrorMessage(error) + ')');
            })   
        },
    }
}
</script>