<template>
    <b-card bg-variant="light">
        <div class="row no-gutters p-1" style="align-items: flex-start; background: antiquewhite;">
            <label for="checkbox" v-show="false">丁単位</label>
            <input
                id="checkbox"
                v-model="enable"
                type="checkbox"
                v-show="false"
            >
            <div class="addressMethod pr-1">
                <b-form-group style="margin-bottom:0;" label="住所特定法" label-class="font-weight-bold" v-slot="{ ariaDescribedby }">
                    <b-form-radio v-model="addressMethod" :aria-describedby="ariaDescribedby" value="box">長方形</b-form-radio>
                    <b-form-radio v-model="addressMethod" :aria-describedby="ariaDescribedby" value="polygon">多角形</b-form-radio>
                </b-form-group>
            </div>
            <div class="buttonBlock pr-1">
                <b-button
                    size="sm"
                    variant="info"
                    class="ml-1"
                    @click="getPosition">
                    <b-icon icon="geo-alt"></b-icon>
                </b-button>
                <b-button
                    size="sm"
                    variant="info"
                    class="ml-1"
                    @click="fitMap">
                    <b-icon icon="textarea-resize"></b-icon>
                </b-button>
                <b-button
                    size="sm"
                    variant="info"
                    class="ml-1"
                    @click="playAudioMessage">
                    <b-icon icon="play-btn"></b-icon>
                </b-button>
            </div>
           <div class="watch d-flex px-1">
                <input
                    v-model="watchInterval"
                >
                <b-button
                    v-show="!watcherId"
                    class="mt-1"
                    size="sm"
                    @click="startWatch">
                    <b-icon icon="play-circle"></b-icon>
                </b-button>
                <b-button
                    v-show="watcherId"
                    class="mt-1"
                    size="sm"
                    @click="stopWatch"
                    variant="danger">
                    <b-icon icon="stop-circle"></b-icon>
                </b-button>
            </div>
            <div
                class="pl-1 d-flex"
                style="color:green;"
                v-if="watcherId">
                計{{timer}}分
            </div>
        </div>
        <span
            class="d-flex mb-1"
            style="background: antiquewhite;"
            :style="isPosError ? 'color:red;':'color:blue;'">
            {{position}}
        </span>

        <l-map
            style="height: 50vh;"
            ref="map">
            <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
            <l-marker ref="trackMarker" :lat-lng="track" @click="clickTrack">
                <l-icon :icon-url="require('@/assets/track.png')" :icon-size="[32,41]"></l-icon>
                <l-popup content="運搬車01"/>
            </l-marker>
            <l-marker v-for="warehouse in warehouses" :lat-lng="warehouse" :key="warehouse.name" @click="clickPlace">
                <l-icon :icon-url="require('@/assets/warehouse.png')" :icon-size="[38,38]"></l-icon>
                <l-popup :content="warehouse.jname"/>
            </l-marker>
            <l-geo-json :geojson="geojson" :options="geojsonOptions" ref="geojson"></l-geo-json>
        </l-map>

        <b-modal ref="modal" hide-footer title="エリア情報">
            <div class="d-block text-center">
                <h3>{{ selectedFeature && (selectedFeature.ward_ja || selectedFeature.GST_NAME) }}</h3>
                <p>{{ selectedFeature }}</p>
            </div>
            <b-button class="mt-3" variant="outline-danger" block @click="hideModal">Close</b-button>
        </b-modal>
        
        <b-table-lite small thead-class="d-none" class="placeInfo mt-1" :items="placeInfo">
            <template #cell(action)="row">
                <b-button @click="takePhoto(row)" variant="info" style="width:45px; height:45px;"><b-icon icon="camera"></b-icon></b-button>
            </template>
        </b-table-lite>
    </b-card>
</template>

<style lang="css" scoped>
/deep/ .placeInfo > tbody > tr > td {
    vertical-align: middle;
    background-color: antiquewhite;
    font-weight: bold;
}
/deep/ .placeInfo > tbody > tr > td:last-child {
    text-align: end;
}
.addressMethod {
    border-width: 0 2px 2px 0;
    border-style: solid;
    border-right-color: white;
    border-bottom-color: white;
}
.buttonBlock {
    height: 81px;
    border-width: 0 2px 2px 0;
    border-style: solid;
    border-right-color: white;
    border-bottom-color: white;
}
.watch {
    width:50px;
    height: 81px;
    flex-direction: column;
    border-width: 0 2px 2px 0;
    border-style: solid;
    border-right-color: white;
    border-bottom-color: white;
}
</style>

<script>
import L from 'leaflet';
import {LMap, LTileLayer, LMarker, LPopup, LIcon, LGeoJson} from 'vue2-leaflet';
import pointInpolygon from 'point-in-polygon';

export default {
    components: {
        LMap,
        LTileLayer,
        LMarker,
        LGeoJson,
        LPopup,
        LIcon
    },
    data() {
        return {
            position: '',
            watchCount: 0,
            watcherId: null,
            watchInterval: 30,
            timer: 0,
            timerId: null,
            isPosError: false,
            enable: false,
            addressMethod: 'box',
            url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
            attribution: '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
            zoom: 15,
            center: { lng: 139.76721063067993, lat: 35.68198566590147 },
            selectedFeature: {},
            geojson: null,
            myLayer: null,
            // geojsonSmall: require('@/assets/tokyo.json'),
            // geojsonAll: require('@/assets/tokyo_all.json'),
            geojsonOptions: {
                style: function style() {
                return {
                    fillColor: '#FC4E2A',
                    // weight: 2,
                    weight: 1,
                    opacity: 1,
                    // color: 'white',
                    color: 'blue',
                    dashArray: '3',
                    // fillOpacity: 0.3
                    fillOpacity: 0.0
                };
                },
                onEachFeature: (feature, layer) => {
                layer.on({
                    // mouseover: this.highlightFeature,
                    // mouseout: this.resetHighlight,
                    click: this.clickFeature
                });
                }
            },
            track: {},
            warehouses: [
                { name: 'WH01', jname: '倉庫01', lng: 139.721120, lat: 35.683332, action: ''},
                { name: 'WH02', jname: '倉庫02', lng: 139.721463, lat: 35.712329, action: ''},
                { name: 'WH03', jname: '倉庫03', lng: 139.765065, lat: 35.694207, action: ''},  
                { name: 'WH04', jname: '倉庫04', lng: 139.743000, lat: 35.671610, action: ''}, 
                { name: 'WH05', jname: '倉庫05', lng: 139.822056, lat: 35.679986, action: ''},
                { name: 'WH06', jname: '倉庫06', lng: 139.822056, lat: 35.679986, action: ''}
            ],
            placeInfo: [],
            audioMessage: new Audio(require('@/assets/message.mp3'))
        }
    },
    // watch: {
    //     enable: function() {
    //         // this.position = '処理中...';
    //         // console.log(this.$refs.position);
    //         if (this.enable) {
    //             // this.geojson = this.geojsonAll;
    //             // this.position = '処理中...';
    //             this.$refs.geojson.mapObject.clearLayers();
    //             this.$refs.geojson.mapObject.addData(this.geojsonAll);
    //             // this.position = '処理完了All';
    //         } else {
    //             // this.geojson = this.geojsonSmall;
    //             // this.position = '処理中...';
    //             this.$refs.geojson.mapObject.clearLayers();
    //             this.$refs.geojson.mapObject.addData(this.geojsonSmall);
    //             // this.position = '処理完了Small';
    //         }
    //     }
    // },
    created() {
        this.loadGeojson();
        // this.geojson = this.geojsonSmall;
        this.updateTrackPosition(this.center);
        // setTimeout(() => {
        //     this.playAudioMessage();
        // }, 4000)
        this.audioMessage.addEventListener('playing', () => {this.position = '再生中...';});
        this.audioMessage.addEventListener('ended', () => {this.position = '';});
        // this.$refs.geojson.mapObject.addEventListener('added', () => {this.position = '処理中...'});
    },
    mounted() {
        // this.watchPosition();
        // this.$nextTick(() => {});
        navigator.serviceWorker.getRegistrations().then(registrations => {
            if (!registrations.length) {
                this.position = 'PWAではありません';
            }
        });
        navigator.serviceWorker.ready.then(registration => {
            if (registration.sync) {
                this.position = 'バックグラウンド処理対応';
            } else {
                this.isPosError = true;
                this.position = 'バックグラウンド処理非対応';
            }
        });
        this.myLayer =  L.geoJSON(null, {
            style: function(feature) {
                if (feature.geometry.type == 'LineString') {
                    return {color: 'green', dashArray: '4'};
                } else {
                    return {color: 'red', fillOpacity: 0.0};
                }
                }
            }).addTo(this.$refs.map.mapObject);
        this.$refs.map.mapObject.panTo(this.center);
        this.fitMap();
    },
    methods: {
        async loadGeojson() {
            await fetch('tokyo.json')
                .then(res => res.json())
                .then(data => {
                    this.geojson = data;
                });
        },
        startWatch() {
            if (this.watcherId) {
                this.stopWatch();
            }
            this.timerId = setInterval(() => {
                this.timer += 1;
                }, 60*1000);
            this.getPosition();
            this.watcherId = setInterval(this.getPosition, this.watchInterval*60*1000);
        },
        stopWatch() {
            clearInterval(this.watcherId);
            clearInterval(this.timerId);
            this.watcherId = null;
            this.timerId = null;
            this.position = '追跡終了';
            this.timer = 0;
            this.watchCount = 0;
        },
        showModal() {
            this.$refs.modal.show();
        },
        hideModal() {
            this.$refs.modal.hide();
        },
        highlightFeature(e) {
            var layer = e.target;
            layer.setStyle({
                fillColor: 'green',
                weight: 5,
                color: '#666',
                dashArray: '',
                fillOpacity: 0.3
            });
            if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
                layer.bringToFront();
            }
        },
        resetHighlight(e) {
            var layer = e.target;
            layer.setStyle({
                fillColor: '#FC4E2A',
                weight: 2,
                opacity: 1,
                color: 'white',
                dashArray: '3',
                fillOpacity: 0.3
            });
            if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
                layer.bringToFront();
            }
        },
        clickFeature(ev) {
            const feature = ev.target && ev.target.feature;
            const featureProps = feature && feature.properties;
            this.selectedFeature = featureProps;
            this.showModal();
        },
        updateTrackPosition(position) {
            this.track =  { name: 'TR01', jname: '運搬車01', lng: position.lng, lat: position.lat, action: ''};
        },
        playAudioMessage() {
            this.audioMessage.currentTime = 0 // 連続で鳴らせるように
            this.audioMessage.play();
        },
        fitMap() {
            let fitMarkers = this.warehouses.concat(this.track);
            this.$refs.map.mapObject.fitBounds(fitMarkers);
        },
        takePhoto(warehouse) {
            alert(`${warehouse.item.jname}で撮影する処理`);
        },
        clickPlace(event) {
            let lat = event.latlng.lat;
            let lng = event.latlng.lng;
            this.placeInfo = this.warehouses.filter(warehouse => warehouse.lat == lat && warehouse.lng == lng);
        },
        clickTrack() {
            this.placeInfo = [this.track];
        },
        getPosition() {
            this.isPosError = false;
            this.position = '位置取得中...';
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(this.positionSuccess, this.positionError, {
                    timeout: 5000
                });
            } else {
                this.isPosError = true;
                this.position = 'ブラウザー非対応';
            }
        },
        watchPosition() {
            if (navigator.geolocation) {
                navigator.geolocation.watchPosition(this.watchPositionSuccess, this.positionError, {
                enableHighAccuracy: true,
                timeout: 1000000,
                maximumAge: 0,
                })
            } else {
                this.isPosError = true;
                this.position = 'ブラウザー非対応';
            }
        },
        positionSuccess(position) {
            this.isPosError = false;
            this.watchCount ++;
            this.position =  `経度: ${position.coords.longitude.toFixed(2)} 緯度: ${position.coords.latitude.toFixed(2)} (${this.watchCount})`;
            // this.setTrackPosition({lng: position.coords.longitude, lat: position.coords.latitude});
            this.updateTrackPosition({lng: position.coords.longitude, lat: position.coords.latitude});
            let adress = this.searchAdress(this.track);
            if (adress) {
                this.position += ` / ${adress}`;
            }
            if (this.myLayer.getLayers().length === 0) {
                this.$refs.map.mapObject.fitBounds([{lng: position.coords.longitude, lat: position.coords.latitude}]);
            } else {
                this.$refs.map.mapObject.fitBounds([this.myLayer.getBounds()]);
            }
        },
        watchPositionSuccess(position) {
            this.isPosError = false;
            this.watchCount ++;
            this.position =  `経度: ${position.coords.longitude.toFixed(2)} 緯度: ${position.coords.latitude.toFixed(2)} (${this.watchCount})`;
            this.setTrackPosition({lng: position.coords.longitude, lat: position.coords.latitude});
        },
        positionError(error) {
            this.isPosError = true;
            let errorInfo = [
				'原因不明のエラーが発生しました',
				'位置情報の取得が許可されません',
				'電波状況などで位置情報が取得できません',
				'位置情報の取得に時間がかかり過ぎてタイムアウトしました'
			] ;
            this.position = `${errorInfo[error.code]}`;
        },
        setTrackPosition(position) {
            this.updateTrackPosition(position);
            this.$refs.map.mapObject.fitBounds([position]);
        },
        getRectFromBounds(bounds) {
            let ne = bounds.getNorthEast();
            let sw = bounds.getSouthWest();
            return [[ne.lng, ne.lat], [ne.lng, sw.lat], [sw.lng, sw.lat], [sw.lng, ne.lat], [ne.lng, ne.lat]];
        },
        searchAdress(latlng) {
            // this.$refs.geojson.mapObject.closePopup();
            this.myLayer.clearLayers();
            var address = '';
            let layers = this.$refs.geojson.mapObject.getLayers();
            if (this.addressMethod == 'polygon') {
                var searchPolygon;
                for (const layer of layers) {
                    for (const polygon of layer.feature.geometry.coordinates) {
                        if (polygon.length == 1) {
                            searchPolygon = polygon[0];
                        } else {
                            searchPolygon = polygon;
                        }
                        if (pointInpolygon([latlng.lng, latlng.lat], searchPolygon)) {
                            this.myLayer.addData(layer.feature);
                            return layer.feature.properties.ward_ja;
                        }
                    }
                }
            } else if (this.addressMethod == 'box') {
                var resultSet = [];
                for (const layer of layers) {
                    if (layer.getBounds().contains(latlng)) {
                        let box = { 
                            "type": "Feature", 
                            "geometry": {
                                "type": "Polygon",
                                "coordinates": [this.getRectFromBounds(layer.getBounds())]
                            }
                        };
                        let center = layer.getCenter();
                        let distance = this.$refs.map.mapObject.distance(latlng, center).toFixed(1);
                        resultSet.push([distance, layer.feature.properties.ward_ja]);
                        let point = { 
                            "type": "Feature", 
                            "geometry": {
                                "type": "Point",
                                "coordinates": [center.lng, center.lat]
                            }
                        };
                        let line = { 
                            "type": "Feature", 
                            "geometry": {
                                "type": "LineString",
                                "coordinates": [[latlng.lng, latlng.lat],[center.lng, center.lat]]
                            }
                        };
                        this.myLayer.addData(box);
                        this.myLayer.addData(point);
                        this.myLayer.addData(line);
                        L.popup().setLatLng(center).setContent(`<div>${layer.feature.properties.ward_ja}(${distance}m)</div>`).addTo(this.myLayer);
                        // address += layer.feature.properties.ward_ja + ',';
                    }
                }
                if (resultSet.length) {
                    return resultSet.sort()[0][1];
                }
            }
            return address;
        }
    }
}

</script>