<template>
    <b-card bg-variant="light">
        <div class="row no-gutters justify-content-center">
            <div class="col col-md-4 text-center"><img v-bind:src="crossVisionF" /></div>
        </div>
        <div class="row no-gutters justify-content-center mb-1" v-if="!this.$store.state.loginData.accessToken">
            <div class="col col-md-4 d-flex justify-content-center"><b-form-input placeholder="ユーザ" v-model="username" @keyup.enter="enterLogin" class="input"></b-form-input></div>
        </div>
        <div class="row no-gutters justify-content-center" v-if="!this.$store.state.loginData.accessToken">
            <div class="col col-md-4 align-self-center d-flex justify-content-center"><b-form-input placeholder="パスワード" v-model="password" type="password" @keyup.enter="enterLogin" class="input"></b-form-input></div>
        </div>
        <div class="row no-gutters justify-content-center">
            <div class="col col-md-4 text-center"><b-button v-if="!this.$store.state.loginData.accessToken" @click="clickLoginFake" @keyup.ctrl.enter="ctrlEnterLogin" class="button my-1">ログイン</b-button></div>
        </div>
            <!-- <tr v-if="this.isShowAccessToken && this.$store.state.loginData.accessToken"><td>
                <b-button @click="logout">ログアウト</b-button>
                <b-table :items="[this.$store.state.loginData]" :fields="fields"></b-table>
                <b-button @click="clickMenu" variant="info">メニュー</b-button>
            </td></tr> -->
        <div id="message" v-if="message">
            <b-alert show variant="danger">{{message}}</b-alert>
        </div>
    </b-card>
</template>

<style scoped>
img {
    width: 100%;
    height: 100%;
}
.form-control {
    width: 60%;
}
</style>

<script>
import axios from 'axios'
import apiUrls from '../common/apiUrls'
import security from '../common/security'
import commonMethods from '../common/commonMethods'

//import bcrypt from 'bcryptjs'
export default {
    data() {
        return {
            username: '',
            password: '',
            fields:  [
                    { key:   'username',    label: 'ユーザ名' }, 
                    { key:   'email',       label: 'メールアドレス' }, 
                    { key:   'password',    label: 'パスワード' }, 
                    { key:   'roles',       label: 'ロール' }, 
                    { key:   'accessToken', label: 'トークン' } 
                    ],
            message: '',
            isShowAccessToken: false,
            crossVisionF: "CrossVisionF.png",
        }
    },
    methods: {
        enterLogin() {
            this.clickLogin() ;
        },
        async clickLoginFake() {
            this.$router.push({path: '/menu'});
        },
        async clickLogin() {
            await this.login();
            if (this.$store.state.loginData.id != '') {
                this.createMenuInfo();
                await this.referNameCollectionMany();
                commonMethods.saveCommonArea('modeTest', false)
                this.$router.push({path: '/menu'});
            }
        },
        async ctrlEnterLogin() {
            this.isShowAccessToken = true;
            await this.login();
        },
        async login() {
            //let hashed_password = bcrypt.hashSync(this.password, 10)
            //let hashed_password = this.getHash(this.password)
            let hashed_password = security.getHash(this.password)
            this.message = ''
            await axios.post(apiUrls.signIn,
            {
                username: this.username,
                password: hashed_password
            })
            .then(response => {
                response.data.password = hashed_password
                this.$store.state.loginData = response.data
                /*
                requestParams.httpRequestHeaders = {
                    Authorization:  'Bearer ' +  this.$store.state.loginData.accessToken,
                    'Content-Type': 'application/json',
                    //'Access-Control-Allow-Origin': '*'
                }*/
            })
            .catch(error => {
                //console.log('error.response.data.code=' + error.response.data.code);
                //console.log('error.response.data.message=' + error.response.data.message);
                // ログイン処理のエラーは、error.response.data.statusにセットされないので、次のとおり表示する。
                this.message = commonMethods.getErrorMessage(error);
            })
        },
        async clickMenu() {
            this.isShowAccessToken = false;
            this.createMenuInfo();
            await this.referNameCollectionMany();
            commonMethods.saveCommonArea('modeTest', true)
            this.$router.push({path: '/menu'});
        },
        logout() {
            this.$store.state.loginData = {
                id:          '',
                username:    '',
                email:       '',
                roles:       [],
                accessToken: ''
            }
            this.$store.rolesResDt = [];
            this.$store.rolesResDt = null;
            this.$store.roleLevelsResDt = [];
            this.$store.roleLevelsResDt = null;
        },
     async referNameCollectionMany() {
            var reqDt = [];
            reqDt.push({nameSection: 'ProgressBackColorScreen'});
            reqDt.push({nameSection: 'VisibleMenuItem'});
            reqDt.push({nameSection: 'VisibleControl'});

            //基本リクエストを作成する
            let payload = {
                transId: "",
                resultCode: null,
                resultMessage: null,
                user: null,
                terminal: null,
                reqDateTime: null,
                resDateTime: null,
                reqDt: reqDt,
            };
            let axiosConfigObject = {
                headers: {
                    Authorization:  'Bearer ' +  this.$store.state.loginData.accessToken,
                    'Content-Type': 'application/json',
                },
            }
            await axios.post(apiUrls.referNameCollectionByPost, payload, axiosConfigObject).then((response) => {
                if(response.data.resultCode == '000') {
                    // console.log('response.data.resultCode=' + response.data.resultCode);
                    // 画面名をキーにした製品と工程の背景色を取得
                    let progressBackColorScreen = commonMethods.convertProgressBackColorScreen('ProgressBackColorScreen', response.data.resDt);
                    // 共通領域に背景色をセーブ
                    commonMethods.saveCommonArea('progressBackColorScreen', progressBackColorScreen);

                    // 画面名をキーにしてメニュ表示の可否を取得
                    let visibleMenuItem = this.getNameSection('VisibleMenuItem', response.data.resDt);
                    let menuInfoByName = commonMethods.loadCommonArea('menuInfoByName');
                    for (let menuName in visibleMenuItem) {
                        if (menuInfoByName[menuName]) {
                            // menuInfoByName[menuName].visible = visibleMenuItem[menuName].nameLong == 'true' ? true : false;
                            menuInfoByName[menuName].visible = visibleMenuItem[menuName] == 'true' ? true : false;
                        }
                    }
                    // メニュ表示の可否を変更したmenuInfoByNameを共通領域にセーブ
                    commonMethods.saveCommonArea('menuInfoByName', menuInfoByName);

                    // 画面名をキーにしてコントロール表示の可否を取得
                    let visibleControlsResDt = this.getNameSection('VisibleControl', response.data.resDt);
                    // console.log('visibleControlByMenuName length=' + Object.keys(visibleControlResDt).length);
                    let visibleControlsByMenuCompName = commonMethods.loadCommonArea('visibleControlsByMenuCompName');
                    if (visibleControlsByMenuCompName == null) {
                        visibleControlsByMenuCompName = {};
                    }
                    for (let menuCompName in visibleControlsResDt) {
                        // console.log('menuName=' + menuName);
                        let visibleControls = visibleControlsByMenuCompName[menuCompName];
                        if (visibleControls == null) {
                            visibleControls = {};
                        }
                        // let nameLongValue = visibleControlResDt[menuName].nameLong;
                        let nameLongValue = visibleControlsResDt[menuCompName];
                        let values = nameLongValue.split(',');
                        for (let value in values) {
                            // console.log('value=' + value + ',values[value]=' + values[value]);
                            let vals = values[value].trim().split('=');
                            let controlName = vals[0].trim();
                            let controlValue = vals[1].trim() == 'true' ? true : false;
                            // console.log('controlName=' + controlName + ',controlValue=' + controlValue);
                            visibleControls[controlName] = controlValue;
                        }
                        visibleControlsByMenuCompName[menuCompName] = visibleControls;
                    }
                    // 画面名をキーにしてコントロール表示の可否をvisibleControlByMenuNameを共通領域にセーブ
                    commonMethods.saveCommonArea('visibleControlsByMenuCompName', visibleControlsByMenuCompName);
                    return;
                } else {
                    this.message = commonMethods.getResponseMessage(response);
                }
            })
            .catch(error => {
                this.message = commonMethods.getErrorMessage(error);
            })
        },
        // メニュのpathからnameとtitle、nameからpathとtitleとvisibleが取得できる連想配列を作成し共通領域にセーブ
        createMenuInfo() {
            let menuInfoByPath = {};
            let menuInfoByName = {};
            let routes = this.$router.options.routes;
            for(let route in routes) {
                // console.log('route=' + route + ', routes[route]=' + routes[route]);
                // console.log('routes[route].path=' + routes[route].path + ', routes[route].name=' + routes[route].name + ', routes[route].meta.title=' + routes[route].meta.title);
                menuInfoByPath[routes[route].path] = {'name': routes[route].name, 'title': routes[route].meta.title}
                menuInfoByName[routes[route].name] = {'path': routes[route].path, 'title': routes[route].meta.title, 'visible': true}
            }
            commonMethods.saveCommonArea('menuInfoByPath', menuInfoByPath);
            commonMethods.saveCommonArea('menuInfoByName', menuInfoByName);
        },
        // resDt.nameSection と resDtNameSection が一致する行の resDt.nameAlpha をキーにして、resDt.nameLongを値とした連想配列を返す。
        // resDt.nameAlphaの値が同じになる行が複数存在する場合、resDt.nameLongの値をカンマで連結する。
        getNameSection(resDtNameSection, resDt) {
            if (resDt != null) {
                let arrayNameSection = {};
                for(let r in resDt) {
                    let row = resDt[r];
                    // console.log('getNameSection() nameSection=' + row['nameSection'] + ', nameAlpha=' + row['nameAlpha'] + ', nameShort=' + row['nameShort'] + ', nameLong=' + row['nameLong']);
                    if (row['nameSection'] == resDtNameSection) {
                        // console.log('getNameSection() nameAlpha=' + row['nameAlpha'] + ', nameLong=' + row['nameLong']);
                        if (arrayNameSection[row['nameAlpha']] == null) {
                            // arrayNameSection[row['nameAlpha']] = {'nameLong': row['nameLong']};
                            arrayNameSection[row['nameAlpha']] = row['nameLong'];
                        } else {
                            arrayNameSection[row['nameAlpha']] = arrayNameSection[row['nameAlpha']] +',' + row['nameLong'];
                        }
                    }
                }
                // console.log('arrayNameSection length=' + Object.keys(arrayNameSection).length);
                return arrayNameSection;
            }
        }        
    }
}
</script>